            title="ÐÐ¾Ð¼ÐµÑ€ Ñ‚ÐµÐ»ÐµÑ„Ð¾Ð½Ð° Ð´Ð¾Ð»Ð¶ÐµÐ½ ÑÐ¾ÑÑ‚Ð¾ÑÑ‚ÑŒ Ð¸Ð· 11-12 Ñ†Ð¸Ñ„Ñ€ Ð¸ Ð¼Ð¾Ð¶ÐµÑ‚ ÑÐ¾Ð´ÐµÑ€Ð¶Ð°Ñ‚ÑŒ Ñ†Ð¸Ñ„Ñ€Ñ‹, Ð¿Ñ€Ð¾Ð±ÐµÐ»Ñ‹, Ñ‚Ð¸Ñ€Ðµ, Ð¿ÑƒÐ·Ð°Ñ‚Ñ‹Ðµ ÑÐºÐ¾Ð±ÐºÐ¸ Ð¸ Ð¼Ð¾Ð¶ÐµÑ‚ Ð½Ð°Ñ‡Ð¸Ð½Ð°Ñ‚ÑŒÑÑ Ñ +"
